.item {
    color: white;

    display: grid;
    grid-template-columns: 60px 1fr;
    align-items: start;
    gap: 8px;

    background-color: #231c43;
    padding: 14px;
    border: 1px solid white;
    border-radius: 10px;
}
.item button {
    justify-self: end;
    grid-column: 2;
    width: 80px;
    height: 30px;
}
.post_text{
    overflow: hidden;
    white-space: pre-wrap;
    /*max-width: 527px;*/
    overflow-wrap: break-word;
}